@import url(https://fonts.googleapis.com/css?family=Lato|Lexend+Tera&display=swap);
body {
  margin: 0px;
  text-align: center;
}

.app {
  font-family: 'Lexend Tera', sans-serif;
  margin: 0;
  padding: 0;
  color: #47424f;
}

.app:after {
  content: "";
  background: url(/pogodareact/static/media/background-wallpaper.c0774ba9.jpg) no-repeat center center;
  background-size: cover;
  opacity: 0.4;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

.app h1 {
  font-size: 3rem;
  color: #fac401;
  text-shadow: 1px 2px 0 #47424f;
}
.app input,
.app button {
    margin: 16px 1px;
    font-size: 16px;
    border: 1px solid #47424f;
    border-radius: 10px;
    background: #ffffff;
    color: #47424f;
}

.app input {
    width: 200px;
    height: 36px;
    padding: 1px 1px 1px 8px;
    cursor: pointer;
    outline: none;
}

.app input:focus {
    outline: none;
}

.app button {
    height: 40px;
    padding: 1px 8px;
    cursor: pointer;
    outline: none;
    -webkit-transition: all .2s;
    transition: all .2s;
}

.app button:hover {
    background: #fac401;
}
.weatherInfo {
    margin: 0px;
    font-family: 'Lato', sans-serif;
}

.weatherInfo hr {
    width: 50%;
}

.weatherInfo h2 {
    font-size: 3rem;
    margin: 24px;
    text-shadow: 1px 2px 0 #fac401;
}

.weatherInfo h3 {
    font-size: 2rem;
    margin: 16px;
    text-shadow: 1px 2px 0 #fac401;
}

.weatherInfo h4 {
    margin: 8px;
}
